<template>
  <div class="app-container" id="gunSim">
    <div class="filter-container">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="卡号">
          <el-input v-model="formInline.iMei" placeholder="卡号" class="filter-item" @keyup.enter.native="getList(1)" />
        </el-form-item>
        <!-- <el-form-item label="续费状态">
          <el-select v-model="formInline.renew" placeholder="续费状态">
            <el-option label="全部" value="0"></el-option>
            <el-option label="正常" value="1"></el-option>
            <el-option label="欠费" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="卡供应商">
          <el-select v-model="formInline.simSupplier" placeholder="卡供应商">
            <el-option label="全部" value="0"></el-option>
            <el-option label="中国移动" value="1"></el-option>
            <el-option label="中国联通" value="2"></el-option>
            <el-option label="中国电信" value="3"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item class="l-btn-item">
          <el-button class="filter-item" type="primary" icon="el-icon-search" @click="getList(1)">搜索</el-button>
        </el-form-item>
        <el-form-item class="l-btn-item">
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName" >
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod" ></el-table-column>
        
        <el-table-column label="设备号" align="center" prop="code"></el-table-column>
        
        <el-table-column label="卡号" width="200px" align="center" prop="imei"></el-table-column>
        
        <el-table-column label="套餐总量" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.total }} M</span>
          </template>
        </el-table-column>

        <el-table-column label="已使用" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.used }} M</span>
          </template>
        </el-table-column>
        
        <el-table-column label="剩余流量" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.left }} M</span>
          </template>
        </el-table-column>
        
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.status=='00'">正常</span>
            <span v-else-if="scope.row.status=='01'">单向停机</span>
            <span v-else-if="scope.row.status=='02'">停机</span>
            <span v-else-if="scope.row.status=='03'">预销号</span>
            <span v-else-if="scope.row.status=='05'">过户</span>
            <span v-else-if="scope.row.status=='06'">休眠</span>
            <span v-else-if="scope.row.status=='07'">待激活</span>
            <span v-else>号码不存在</span>
          </template>
        </el-table-column>
        
        <el-table-column label="套餐名称" align="center">
          <template slot-scope="scope">
            <p v-for="item in scope.row.products" :key="item.id">
              {{item.prodname}}
            </p>
          </template>
        </el-table-column>

        <el-table-column label="套餐开始时间" align="center">
          <template slot-scope="scope">
            <p v-for="item in scope.row.products" :key="item.id">
              {{item.prodinstefftime}}
            </p>
          </template>
        </el-table-column>

        <el-table-column label="套餐结束时间" align="center">
          <template slot-scope="scope">
            <p v-for="item in scope.row.products" :key="item.id">
              {{item.prodinstexptime}}
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList()" layout="prev, pager, next, sizes, total, jumper" />
  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
//枪支列表，添加枪支，编辑，删除，搜索，批量添加枪支
import { gunCardPage,gunList,gunSearch} from "../../../api/managementArchives/gunFiles";
//大队下拉框数据，人员下拉框数据
import { exportExcelReq } from "../../../api/managementArchives/common";

export default {
  name: "gunSim",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //枪支列表
      total: 0, //枪支总数
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      formInline:{// 搜索条件
        iMei:'',
        renew:'0',
        simSupplier:'0'
      }
    };
  },
  created() {
    this.getList(); //初始化获取枪支列表
  },
  methods: {
    getList(page) {
      if(page){// 点击搜索
        this.listQuery.page = page;
      };
      let _t = this;
      _t.listLoading = true;
      gunCardPage({
        keyword:this.formInline.iMei,
        page: this.listQuery.page,
        limit: this.listQuery.limit
      }).then(res => {
        if (res.status == 0) {
          this.list = res.data;
          this.total = res.count;

          this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
        } else {
          this.$message.error(res.msg);
        }
      }).finally(() => {
        _t.listLoading = false;
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 16,title:"枪支sim卡信息" }).then(res => {
         try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let fileName = "枪支sim卡信息" + ".xls";
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
    }
  }
};
</script>
<style lang="scss">
#gunSim {
  .el-button--mini {
    padding-left: 4px;
    padding-right: 4px;
  }
  .el-dialog{
    .el-input__inner::placeholder {
      font-size: 12px;
    }
  }
  .filter-container {
    .el-form {
      display: flex;
      .el-form-item {
        width: 240px;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        .el-input,.el-input__inner {
          width: 160px;
        }
        &.l-btn-item {
          width: auto;
        }
      }
    }
  }
  .el-table .cell p {
    margin: 2px 0;
    line-height: 21px;
    white-space: nowrap;
  }
}
</style>




