import request from '../../request'
//枪支列表
export function gunList(data) {
    return request({
        url: '/guns/list',
        method: 'post',
        data
    })
}
//添加枪支
export function gunAdd(data) {
    return request({
        url: '/guns/add',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//修改枪支
export function gunUpdate(data) {
    return request({
        url: '/guns/update',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//删除枪支
export function gunDelete(data) {
    return request({
        url: '/guns/scrap',
        method: 'post',
        data
    })
}
//根据枪支编号搜索枪支
export function gunSearch(data) {
    return request({
        url: '/guns/query',
        method: 'post',
        data
    })
}
//批量添加枪支
export function gunAddBatch(data) {
    return request({
        url: '/guns/addBatch',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
